.stars {
  @extend %flex-display;

  &__star {
    width: 16px;
    height: 16px;
    margin-right: 4px;
    background-image: url(/images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
  }

  &--4 > :nth-child(-n+4) {
    background-image: url(/images/star-active.svg);
  }
}
